import React, { useState } from "react";
import "./FormPage.css";
import Header from "../components/Header";
import { useNavigate, useParams } from "react-router-dom";
import { privateMemoryAccess } from "../api/api";

const PrivateMemoryAccessPage = () => {
  const { groupId, postId } = useParams();
  const [password, setPassword] = useState("");
  const [isError, setIsError] = useState(false);
  const navigate = useNavigate();

  const handleSubmit = async (event) => {
    event.preventDefault();
    
    try {
      const request = { password };

      const success = await privateMemoryAccess(postId, request);
      if(success) {
        navigate(`/groups/posts/${postId}`);
      } else {
        setIsError(true);
        setPassword(""); //  
      }
    } catch (e) {
      console.error(' : ',e);
      setIsError(true);
      setPassword(""); //  
    }
  };

  return (
    <div className="create-content">
      <Header />
      <h2> </h2>
      <h5>      .</h5>
      <form name="MemoryCreateForm" className="create-form" onSubmit={handleSubmit}>
          <div className="input-group">
            <label htmlFor="delete-password">  </label>
            <input 
              id="delete-password"
              type="password" 
              value={password}
              onChange={(e) => {
                setPassword(e.target.value);
                setIsError(false); //     
              }}
              placeholder={isError ? " .   ." : "  "}
              required
              className={isError ? "error" : ""}
            />
          </div>
        <button type="submit" className="submit-button"></button>
      </form>
    </div>
  );
};

export default PrivateMemoryAccessPage;
