import axios from "axios";

export const fetchMemory = async (postId) => {
  try {
    const response = await fetch(
      `https://codeit-zogakzip-backend.onrender.com/api/posts/${postId}`
    );
    if (!response.ok)
      throw new Error("    .");
    return await response.json();
  } catch (error) {
    console.error(error);
    return null;
  }
};

