import React, { useState } from "react";

const CommentDeleteModal = ({ onClose, onSubmit, isError }) => {
  const [password, setPassword] = useState("");

  const handleSubmit = (event) => {
    event.preventDefault(); //    

    const commentData = { password };
    onSubmit(commentData);
  };

  return (
    <div className="modal-overlay">
      <div className="modal-content">
        <button className="close-button" onClick={onClose}></button>
        <h2> </h2>

        <form name="CommentDeleteForm" className="modal-form" onSubmit={handleSubmit}>
          <div className="input-group">
            <label>  </label>
            <input 
            id="password"
            type="password" 
            placeholder="  " 
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
            className={isError ? "error" : ""}
            />
          </div>
        

        <button type="submit" className="comment-delete-button"></button>
        </form>
      </div>
    </div>
  );
};

export default CommentDeleteModal;
