import { useState } from 'react';
import { Link } from 'react-router-dom';
import styles from './Nav.module.css';
import logo from '../assets/logo.svg';
import searchIcon from '../assets/search.svg';

const Nav = ({ showTabs = false, showSearch = false, showCreateButton = false, showSortDropDown = false, setSelectedTab, setSortBy, setSearchQuery }) => {
    const [activeTab, setActiveTab] = useState('');

    const handleTabClick = (tab) => {
        setActiveTab(tab);
        setSelectedTab(tab);
    };

    return (
        <nav className={styles.navbar}>
            <div className={styles.topContainer}>
                <div className={styles.logoWrapper}>
                    {/*  */}
                    <Link to="/groups" className={styles.logo}>
                        <img src={logo} alt=" " className={styles.logoImage} />
                    </Link>
                </div>
                {/*    */}
                {showCreateButton && (
                    <Link to="/groups/create" className={styles.createButton}>
                         
                    </Link>
                )}
            </div>
            <div className={styles.bottomContainer}>
                <div className={styles.navLeft}>
                    {/* /  (  ) */}
                    {showTabs && (
                        <div className={styles.tabs}>
                            <button
                                className={activeTab === "public" ? styles.activeTab : ""}
                                onClick={() => handleTabClick("public")}
                            >
                                
                            </button>
                            <button
                                className={activeTab === "private" ? styles.activeTab : ""}
                                onClick={() => handleTabClick("private")}
                            >
                                
                            </button>
                        </div>
                    )}
                </div>
                <div className={styles.navCenter}>
                    {/*   (  ) */}
                    {showSearch && (
                    <div className={styles.searchContainer}>
                        <img src={searchIcon} alt=" " className={styles.searchIcon} />
                        <input type="text" placeholder="  " className={styles.searchBar} onChange={(e) => setSearchQuery(e.target.value)}/>
                    </div>
                )}
                </div>
                <div className={styles.navRight}>
                    {/*   ( ) */}
                    {showSortDropDown && (
                        <select 
                            className={styles.sortDropDown}
                            onChange={(e) => setSortBy(e.target.value)}>
                            <option value="mostLiked"></option>
                            <option value="lastest"></option>
                            <option value="mostPosted">  </option>
                            <option value="mostBadge"> </option>
                        </select>
                    )}
                </div>
            </div>
        </nav>
    );
};

export default Nav;