import { useState, useEffect } from 'react';
import { useNavigate, useParams, Link } from 'react-router-dom';
import styles from './PrivateGroupAccessPage.module.css';
import { privateGroupAccess, verifyPassword } from '../api/api';
import logo from '../assets/logo.svg';

const PrivateGroupAccessPage = () => {
  const { groupId } = useParams();
  console.log(" [DEBUG] useParams()  groupId:", groupId);
  const [password, setPassword] = useState("");
  const [errorMessage, setErrorMessage] = useState(null);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();
    console.log(" [DEBUG]  groupId:", groupId);
    console.log(" [DEBUG]  password:", password);
    console.log(" [DEBUG]  groupId:", groupId);
    console.log(" [DEBUG]  password:", password);

    try {
      const request = { password }; //    
      const response = await verifyPassword(groupId, request);
      
      console.log(" [DEBUG] API :", response); //  API  

      if (response?.message === " .") {
        navigate(`/groups/${groupId}`);
      } else if (response?.message === " .") {
        setIsModalOpen(true);
        setErrorMessage(" .");
        setPassword("");
      } else {
        setErrorMessage("    .");
        setIsModalOpen(true);
      }
    } catch (error) {
      console.error(" [DEBUG]   :", error);
      setErrorMessage("    .");
      setIsModalOpen(true)
      setPassword(""); //  
    }
  };

  return (
    <div className={styles.pageContainer}>
      {/*   */}
      <Link to="/groups">
        <img src={logo} alt=" " className={styles.logo} />
      </Link>

      <div className={styles.container}>
        <h1 className={styles.title}> </h1>
        <p className={styles.description}>      .</p>
        
        <form onSubmit={handleSubmit} className={styles.form}>
          <input
            type="password"
            placeholder="   "
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            className={styles.input}
          />
          <button type="submit" className={styles.submitButton}></button>
        </form>

        {/*    */}
        {isModalOpen && (
          <div className={styles.modalOverlay}>
            <div className={styles.modalContainer}>
              <h2 className={styles.modalTitle}>   </h2>
              <p className={styles.modalText}>  .</p>
              <button 
                className={styles.modalButton}
                onClick={() => setIsModalOpen(false)}
              >
                
              </button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default PrivateGroupAccessPage;