import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import styles from './CreateGroupPage.module.css';
import logo from '../assets/logo.svg';
import { createGroup, imageToUrl } from "../api/api";

const CreateGroupPage = () => {
  const navigate = useNavigate();
  const [groupName, setGroupName] = useState('');
  const [introduction, setIntroduction] = useState('');
  const [isPublic, setIsPublic] = useState(true);
  const [password, setPassword] = useState('');
  const [image, setImage] = useState(null);
  const [imageUrl, setImageUrl] = useState('');
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [modalMessage, setModalMessage] = useState('');

  const uploadImage = async (file) => {
    if (!file || !(file instanceof File)) return null;
    console.log(file);
    try {
      const response = await imageToUrl(file);
      return response;
    } catch (error) {
      console.error('  :', error);
      return null;
    }
  };


  //   
  const handleFileChange = async (event) => {
    const file = event.target.files[0];
    if (file) {
      setImage(file);
  
      try {
        const uploadedImageUrl = await imageToUrl(file); //   
        if (uploadedImageUrl) {
          setImageUrl(uploadedImageUrl); //   URL 
          console.log(" [Uploaded Image URL]:", uploadedImageUrl);
        } else {
          throw new Error(" URL   .");
        }
      } catch (error) {
        console.error("   :", error);
        setModalMessage("  .");
        setIsModalOpen(true);
      }
    }
  };
  
  

  //   API 
/*  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!groupName) {
      setModalMessage(" .");
      setIsModalOpen(true);
      return;
    }

    const formData = new FormData();
    formData.append("name", groupName);
    formData.append("introduction", introduction);
    formData.append("isPublic", isPublic);
    if (!isPublic) formData.append("password", password);
    if (image) formData.append("imageUrl", image);

    const groupData = {
      groupName,
      password,
      imageUrl: imageUrl || "https://example.com/default-image.jpg",
      isPublic,
      introduction,
    };

    console.log(" [Request Body]:", groupData);

    try {
      const createdGroup = await createGroup(groupData);
      if (createdGroup) {
        console.log("   :", createdGroup);
        navigate(`/group/public`);
      }
    } catch (error) {
      console.error("Error:", error);
      setModalMessage(error.message || "  .");
      setIsModalOpen(true);
    }
  }; */

  const handleSubmit = async (event) => {
      event.preventDefault();
      
      if (image) {
        const url = await uploadImage(image)
        console.log(url);
        setImageUrl(url);
      }

      const groupData = {
        name: groupName,
        password: password,
        imageUrl: imageUrl,
        isPublic,
        introduction,
      };
        
      console.log(" [Request Body]:", groupData);

      try {
        const createdGroup = await createGroup(groupData);
        if (createdGroup) {
          console.log("   :", createdGroup);
          navigate(`/groups/${createdGroup.id}`);
        }
      } catch (error) {
        console.error("Error:", error);
        setModalMessage(error.message || "  .");
        setIsModalOpen(true);
      }
    };


  //   
  const handleCloseModal = () => {
    setIsModalOpen(false);
    if (modalMessage === "  ") {
      navigate('/');
    }
  };

  return (
    <div className={styles.container}>
      <Link to="/groups"><img src={logo} alt=" " className={styles.logo} /></Link>
      <h1 className={styles.title}> </h1>
      <form onSubmit={handleSubmit} className={styles.form}>
        {/*   */}
        <label className={styles.label}></label>
        <input 
          type="text" 
          placeholder=" "
          value={groupName} 
          onChange={(e) => setGroupName(e.target.value)}
          className={styles.input}
        />

        {/*    */}
        <label className={styles.label}> </label>
        <div className={styles.imageUploadContainer}>
          <div className={styles.fileUpload}>
            <input 
              type="text"
              placeholder="  "
              readOnly
              className={styles.fileInput}
              value={image ? image.name : ""}
            />
            <label className={styles.fileButton}>
               
              <input type="file" onChange={handleFileChange} className={styles.hiddenFileInput} />
            </label>
          </div>
          {image && <img src={URL.createObjectURL(image)} alt="" className={styles.previewImage} />}
        </div>

        {/*   */}
        <label className={styles.label}> </label>
        <textarea
          placeholder=" "
          value={introduction} 
          onChange={(e) => setIntroduction(e.target.value)}
          className={styles.textarea}
        />

        {/*   */}
        <div className={styles.toggleContainer}>
          <span className={styles.toggleLabel}>  </span>
          <div className={styles.switchContainer}>
            <span className={isPublic ? styles.active : ''}>{isPublic ? "" : ""}</span>
            <label className={styles.switch}>
              <input
                type="checkbox"
                checked={isPublic}
                onChange={() => setIsPublic(!isPublic)}
              />
              <span className={styles.slider}></span>
            </label>
          </div>
        </div>

        {/*  */}
        
        <div>
          <label className={styles.label}></label>
          <input
            type="password"
            placeholder=" "
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            className={styles.input}
          />
        </div>
        

        {/*   */}
        <button type="submit" className={styles.createButton}></button>
      </form>

      {/*  */}
      {isModalOpen && (
        <div className={styles.modalOverlay}>
          <div className={styles.modal}>
            <h2>{modalMessage}</h2>
            <p>{modalMessage === "  "
              ? "  ."
              : "  ."}
            </p>
            <button onClick={handleCloseModal} className={styles.modalButton}></button>
          </div>
        </div>
      )}
    </div>
  );
};

export default CreateGroupPage;