import React, { useState } from "react";
import "./MemoryEditModal.css";
import { imageToUrl } from "../api/api";

const MemoryEditModal = ({ onClose, onSubmit, initialData }) => {
  const [nickname, setNickname] = useState(initialData?.nickname || "");
  const [title, setTitle] = useState(initialData?.title || "");
  const [tags, setTags] = useState(initialData?.tags || "");
  const [tagInput, setTagInput] = useState(""); //   
  const [location, setLocation] = useState(initialData?.location || "");
  const [image, setImage] = useState(null);
  const [imageUrl, setImageUrl] = useState(initialData?.imageUrl || "");
  const [content, setContent] = useState(initialData?.content || "");
  const [moment, setMoment] = useState(initialData?.moment || "");
  const [isPublic, setIsPublic] = useState(initialData?.isPublic || false);
  const [postPassword, setPassword] = useState("");

  //   
  const handleTagInputChange = (e) => {
    setTagInput(e.target.value);
  };

  //      
  const handleTagKeyDown = (e) => {
    if (e.key === "Enter" && tagInput.trim() !== "") {
      e.preventDefault(); //   
      if (!tags.includes(tagInput.trim())) {
        setTags([...tags, tagInput.trim()]);
      }
      setTagInput(""); //   
    }
  };

  //   
  const removeTag = (tagToRemove) => {
    setTags(tags.filter((tag) => tag !== tagToRemove));
  };

  const handleImageUpload = async (file) => {
    if (!file || !(file instanceof File)) return;
    try {
      const uploadedUrl = await imageToUrl(file);
      setImageUrl(uploadedUrl);
    } catch (error) {
      console.error("  :", error);
    }
  };

  //   
  const handleSubmit = async (event) => {
    event.preventDefault();

    if (image) {
      await handleImageUpload(image);
    }

    onSubmit({
      nickname,
      title,
      tags,
      location,
      imageUrl,
      content,
      moment,
      isPublic,
      postPassword,
    });
  };

  return (
    <div className="editmodal-overlay">
      <div className="editmodal-content">
        <button className="close-button" onClick={onClose}></button>
        <h2> </h2>
        <form name="MemoryEditForm" className="editmodal-form" onSubmit={handleSubmit}>
          <div className="editform-body">
            <div className="left-section">
              <div className="input-group">
                <label></label>
                <input type="text" value={nickname} onChange={(e) => setNickname(e.target.value)} placeholder="  " required />
              </div>
              <div className="input-group">
                <label></label>
                <input type="text" value={title} onChange={(e) => setTitle(e.target.value)} placeholder="  " required />
              </div>
              <div className="input-group">
                <label></label>
                <input type="file" onChange={(e) => setImage(e.target.files[0])} />
              </div>
              <div className="input-group">
                <label></label>
                <textarea value={content} onChange={(e) => setContent(e.target.value)} placeholder="   " required />
              </div>
            </div>

          <div className="separator"></div>

            <div className="right-section">
              <div className="input-group">
                <label></label>
                <input
                  type="text"
                  value={tagInput}
                  onChange={handleTagInputChange}
                  onKeyDown={handleTagKeyDown}
                  placeholder="   Enter"
                />
                {/*   */}
                <div className="tag-container">
                  {tags.map((tag, index) => (
                    <span key={index} className="tag">
                      #{tag} <button className="remove-tag" onClick={() => removeTag(tag)}></button>
                    </span>
                  ))}
                </div>
              </div>
              <div className="input-group">
                <label></label>
                <input type="text" value={location} onChange={(e) => setLocation(e.target.value)} placeholder="  " />
              </div>
              <div className="input-group">
                <label> </label>
                <input type="date" value={moment} onChange={(e) => setMoment(e.target.value)} />
              </div>
              <div className="input-group toggle-group">
                <label> </label>
                <label className="switch">
                  <input type="checkbox" checked={isPublic} onChange={() => setIsPublic(!isPublic)} />
                  <span className="slider"></span>
                </label>
                <span>{isPublic ? "" : ""}</span>
              </div>
              <div className="input-group">
                <label></label>
                <input type="password" value={postPassword} onChange={(e) => setPassword(e.target.value)} placeholder="  " required />
              </div>
            </div>
          </div>

          <button type="submit" className="submit-button"></button>
        </form>
      </div>
    </div>
  );
};

export default MemoryEditModal;
