import React, { useState, useEffect } from "react";
import "./CommentModal.css"; //   import

const CommentModal = ({ mode, initialData = null, onClose, onSubmit, isError }) => {
  const [id, setId] = useState(null);
  const [nickname, setNickname] = useState("");
  const [content, setContent] = useState("");
  const [password, setPassword] = useState("");  

  //     
  useEffect(() => {
    if (mode === "edit" && initialData) {
      setId(initialData.id);
      setNickname(initialData.nickname);
      setContent(initialData.content);
    }
  }, [mode, initialData]);

  const handleSubmit = (event) => {
    event.preventDefault(); //    

    const commentData = { nickname, content, password };
    if (mode === "edit") {
      onSubmit(id, commentData); //   id 
    } else {
      onSubmit(commentData); //   id 
    }
  };

  return (
    <div className="modal-overlay">
      <div className="modal-content">
        <button className="close-button" onClick={onClose}></button>
        <h2>{mode === "edit" ? " " : " "}</h2>

        <form name="CommentForm" className="modal-form" onSubmit={handleSubmit}>
          <div className="input-group">
            <label htmlFor="nickname"></label>
            <input
              id="nickname"
              type="text"
              value={nickname}
              onChange={(e) => setNickname(e.target.value)}
              placeholder="  "
              required
              disabled={mode === "edit"}
            />
          </div>
          <div className="input-group">
            <label htmlFor="content"></label>
            <textarea
              id="content"
              value={content}
              onChange={(e) => setContent(e.target.value)}
              placeholder="   "
              required
            />
          </div>
          <div className="input-group">
            <label htmlFor="password"></label>
            <input
              id="password"
              type="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              placeholder="  "
              required
              className={isError ? "error" : ""}
            />
          </div>

          <button type="submit" className="submit-button">
            {mode === "edit" ? "" : ""}
          </button>
        </form>
      </div>
    </div>
  );
};

export default CommentModal;
